.main__container {
    width: 100%;
    height: 100%;
    padding-bottom: 3.125rem;
}

.header {
    width: 100%;
    height: 100%;
    max-height: 6.25rem;
}

.header__inner {
    width: 100%;
    height: 100%;
    padding: 40px;
}

.header__title {
    width: 100%;
    white-space: nowrap;
    text-align: center;
    font-size: 2.5rem;
    font-weight: 700;
}

.container {
    width: 100%;
    height: 100%;
    margin-top: 2.5rem;
    padding: 1.25rem 5rem;
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    box-sizing: border-box;
}

.active__orders__item {
    width: 50%;
}

@media only screen and (max-width: 1000px) {
    .container {
        padding: 1.25rem;
    }
}

@media only screen and (max-width: 700px) {
    .header__title {
        font-size: 2rem;
    }
    .active__orders__item {
        width: 100%;
    }
}

@media only screen and (max-width: 400px) {
    .header__title {
        font-size: 1.8rem;
    }
}
