.container {
    width: 18.75rem;
    height: 4.6875rem;
    flex-grow: 1;
    border-radius: 0.625rem;
    position: relative;
    /* border: 0.0625rem solid #ccc; */
    box-sizing: border-box;
}

.value__container {
    height: 4.6875rem;
    margin: 1.25rem;
    padding: 1.25rem;
    display: flex;
    align-items: center;
    cursor: pointer;
    border: none;
    border-radius: 2.5rem;
    background-color: #e9e9ff;
    box-shadow: 0.625rem 0.625rem 0.625rem 0rem rgba(0, 0, 0, 0.2);
    font-size: 1.25rem;
    color: #334669;
}

.menu {
    position: absolute;
    width: 105%;
    top: 7 0.3125rem;
    margin-left: -2.5%;
    z-index: 20;
    max-height: 18.75rem;
    overflow: auto;
    background: #fff;
}

.option {
    padding: 0.9375rem;
    background: #f0f0f0;
    border-bottom: 0.0625rem solid #ccc;
    cursor: pointer;
    transition: 0.2s;
}

.option:hover {
    background: #ccc;
}

.option__text {
    font-weight: 300;
}

.search__input {
    height: 100%;
    width: 100%;
    background: none;
    padding: 0 1.25rem;
    box-sizing: border-box;
    font-size: 0.9375rem;
    color: #404040;

    font-family: Nunito;
    border: none;
}

.search__input:focus {
    outline: none;
}

.value__text {
    color: #404040;
    font-size: 0.9375rem;
    font-family: Nunito;
}

.label {
    font-weight: 300;
    margin-bottom: 0.25rem;
    color: #7b7b7b;
}

.overlay {
    position: fixed;
    top: 0;
    left: 0;
    /* background: #000; */
    width: 100vw;
    height: 100vh;
}

