input {
  height: 4.6875rem;
  margin: 1.25rem;
  padding: 1.25rem;
  border: none;
  border-radius: 2.5rem;
  background-color: #e9e9ff;
  box-shadow: inset 0.3125rem 0.3125rem 0.625rem 0rem rgba(0, 0, 0, 0.2);
  font-size: 1.25rem;
}

.form {
  width: 100%;
  padding: 1.25rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.form__wrapper {
  width: 100%;
  max-width: 37.5rem;
  padding: 1.25rem 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.form__inputs {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.form__inputs__wrapper {
  width: 100%;
}

.form__delivery__price__input {
  width: 100%;
}

.form__inputs__delivery__row {
  width: 100%;
  margin: 1.25rem 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.form__delivery__price__title,
.form__delivery__time__title {
  margin: 0.3125rem 0;
  font-size: 1.25rem;
  font-weight: 700;
}

.form__delivery__from,
.form__delivery__to {
  font-size: 1.25rem;
  font-weight: 700;
}

.form__delivery__time__input {
  width: 12.5rem;
}

.form__switch {
  width: 100%;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
}

.form__switch__title {
  padding: 1.25rem;
  font-size: 1.25rem;
  font-weight: 700;
}

.form__switch__button input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.form__switch__button {
  padding: 0 0.625rem;
  display: flex;
  align-items: center;
  font-size: 1.25rem;
  height: 3.125rem;
  width: 6.25rem;
  background-color: #e9e9ff;
  box-shadow: inset 0.3125rem 0.3125rem 0.625rem 0rem rgba(0, 0, 0, 0.2);
  border-radius: 2rem;
  cursor: pointer;
}

.form__switch__button div {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 2rem;
  background: #fe805f;
  box-shadow: 0 0.1em 0.3em rgba(0, 0, 0, 0.3);
  -webkit-transition: all 300ms;
  -moz-transition: all 300ms;
  transition: all 300ms;
}

.form__switch__button input:checked + div {
  background-color: #334669;
  -webkit-transform: translate3d(100%, 0, 0);
  -moz-transform: translate3d(100%, 0, 0);
  transform: translate3d(100%, 0, 0);
}

.form__submit__button__row {
  width: 100%;
  padding: 0 10px;
  text-align: center;
}

.form__submit__button {
  width: 100%;
  max-width: 40rem;
  height: 4.6875rem;
  border: none;
  border-radius: 0.625rem;
  background-color: #fe805f;
  font-size: 1.25rem;
  color: #fff;
  cursor: pointer;
  transition: 0.2s linear;
}

.form__submit__button:hover {
  opacity: 0.8;
  box-shadow: 0.125rem 0.125rem 0.25rem 0rem rgba(0, 0, 0, 0.3);
}

.form__submit__button:focus {
  outline: none;
}

@media only screen and (max-width: 700px) {
  input {
    height: 3.5rem;
    margin: 1.25rem 0;
  }

  .form {
    padding: 1rem 0;
  }

  .form__inputs {
    width: 100%;
  }

  .form__delivery__time__input {
    width: 10rem;
  }
}
